export default {"name":"白兰花茶","img":"\\zaijiagongcha\\bailanhuacha\\img_201911221335220pteih.png","desc":["白兰花茶，属于花茶，主要原料为白兰花，纯粹的白兰花茶香浓烈、持久，滋味浓厚。主销山东、陕西等地，是仅次于茉莉花茶的又一大宗花茶产品。其主要产地为广州、苏州、福州、成都等地。年产量2000--3000吨。其特点清香爽口，芳香化湿，具有利尿化痰，镇咳平喘的功效。"],"menus":[{"name":"白兰花茶茶叶历史","content":[{"text":"白兰\n        花茶\n        ，是除茉莉花茶外的又一个花茶的代表品种，主要产于广州、福州、苏州、金华、成都等地。用白兰花窨茶已有悠久的历史，纯粹的白兰花花香浓烈、持久，滋味浓厚。"},{"src":"\\zaijiagongcha\\bailanhuacha\\img_20191122133522bncbcc.jpg"},{"text":"白兰花茶的主要原料是白兰花，也叫“缅桂”，它是一种夏秋开的白花，以夏季最盛。其次也有用同属之黄兰(亦称“黄桷兰”)、含笑等的。白兰，叶革质，卵状披针形或长椭圆形，全缘，叶柄长1.5厘米，基部楔形。花单生于叶腋，夏秋开白花，花被8～10枚，披针形，有馥郁香气，4月下旬到9月陆续开放，而以夏季最盛。黄兰亦称黄缅桂、黄桷兰，外形与白兰极为相似，唯其花为淡黄色，叶柄上托叶痕较长，叶背有毛，亦是优良的窨茶香花。"}]},{"name":"白兰花茶茶叶特点","content":[{"text":"白兰花香浓郁持久，是窨制烘青\n        绿茶\n        的主要原料，白兰\n        花茶\n        的特征是，外形条索紧结重实，色泽墨绿尚润，香气鲜浓持久，滋味浓厚尚酵，汤色黄绿明亮，叶底嫩匀明亮。 含笑花香气清幽隽永，窨制高级供青类名茶，其外形条索紧细匀整，色泽翠绿油润，香气清纯隽永，滋味鲜爽回甘，汤色黄绿清澈，叶底嫩黄柔软。"},{"src":"\\zaijiagongcha\\bailanhuacha\\img_20191122133522a8cp5q.jpg"}]},{"name":"白兰花茶加工工艺","content":[{"text":"白兰\n        花茶\n        多以中、低档烘青茶坯作原料，其窨制技术主要有鲜花养护、茶坯处理、窨花拌和与匀堆装箱等四步。"},{"text":"茶坯处理"},{"text":"白兰花的香气虽浓郁持久，但不及茉莉花芬芳幽雅鲜灵，故不适合窑制高级茶，只窑制中、低级茶，茶片、茶末或作为茉莉花茶“打底”用花。白兰花茶窑制前，茶坯需进行处理，使之达到一定的干燥程度和坯温，在窑花中促进鲜花香气挥发和茶坯最大限度吸收花香。茶坯处理首先是复火，烘干机进口温度掌握在120℃左右，烘8-12分钟，复火后茶茶坯含水量严格控制在4.5-5%之间，窑花前茶坯温度控制在30-35℃之间，一般比室温高3-8℃。"},{"src":"\\zaijiagongcha\\bailanhuacha\\img_20191122133522fnj9xr.jpg"},{"text":"鲜花处理"},{"text":"白兰鲜花进厂后，及时摊凉，防止鲜花因呼吸热的积累而使花温上升，造成香气挥发和花朵变红。摊凉宜选择清凉、荫凉、通风的场地，厚度约3-5厘米，雨水花还应摊得薄一些。然后再折瓣(除去花蒂、花蕊)或切碎及时进行窑花拼和。"},{"text":"配花量"},{"text":"白兰花茶配花量的多少，应根据茶坯质量，鲜花含水量和成品出厂水分标准而定。白兰花配花量每100斤茶坯用5-6斤，级内茶坯用7-8斤，级外茶坯(茶片、茶末、茶梗)用花3-4斤。窑花前茶坯含水量，级内茶4-4.5%，级外茶4.5-5%。这样才能使成品茶的含水量符合出厂规定8.5-9.5%的标准，又能使花茶品质达到质量要求。"},{"text":"窑花拼合"},{"text":"白兰花茶窑制工艺较茉莉花茶简单，窑制时，一般采用折瓣窑或切碎窑，其中以折瓣窑制品质最好，但在生产高峰期间，白兰花数量多，折瓣窑制有困难，往往采用切碎窑制，虽效果好，但切口容易变红，影响成品茶品质。为了减少白兰花切口变红和保持花茶香气纯正鲜爽，一般应边切碎边上堆窑花拼合，然后将拼合后的茶坯堆入囤内耙匀整平。在正常情况，窑花拼合后，经过6-8小时进行匀堆装箱。"},{"text":"匀堆装箱"},{"text":"开堆装箱时，应再进行一次拼合，注意使花瓣与茶坯混合均匀，装好一箱随即将盖头纸盖好，并将铝罐头折叠平整，每装一批应在当天把全部箱盖封好，以利茶坯吸收花香和防止香气挥发散失。"}]},{"name":"白兰花茶冲泡方法","content":[{"text":"用白兰花两朵，\n        绿茶\n        3g，先将白兰花洗净，与绿茶一同放入茶杯中，加入沸水冲泡。"}]},{"name":"白兰花茶功效作用","content":[{"text":"白兰花含有芳香性挥发油、抗氧化剂和杀菌素等物质，整个花可以直接入药，对于慢性支气管炎、虚劳久咳、前列腺炎等效果极佳。"},{"text":"1.清热解暑"},{"text":"若取玉兰花数朵，茶叶少许，放杯内泡茶饮，可治中暑头晕胸闷。亦可取玉兰花、苏叶各适量，以开水冲泡代茶饮，治感冒头痛。"},{"text":"2.散寒通窍"},{"text":"白兰花用酒精浸泡过滤后，浓缩成稠状浸膏，或将辛夷制成煎剂、油剂、乳剂之类，以棉条浸透塞鼻，对肥厚性鼻炎和急性鼻炎均有显著疗效，使鼻腔通气舒畅，分泌物减少，头痛消失，鼻甲肥大改善。"},{"text":"3.行气化浊"},{"text":"白兰花能行气化浊，止咳。常用于治前列腺炎、妇女白带、小儿支气管炎、虚劳久咳等症。"},{"text":"4.调经助育"},{"text":"临床验证，将白兰花加入辨证复方内，每次用鲜品5朵或干品3克，治功能性不育或原发性痛经有一定疗效。"}]},{"name":"白兰花茶鉴别好茶","content":[{"text":"白兰花香浓郁持久，是窨制烘青\n        绿茶\n        的主要原料，白兰\n        花茶\n        的特征是，外形条索紧结重实，色泽墨绿尚润，香气鲜浓持久，滋味浓厚尚酵，汤色黄绿明亮，叶底嫩匀明亮。"},{"text":"含笑花香气清幽隽永，窨制高级供青类名茶，其外形条索紧细匀整，色泽翠绿油润，香气清纯隽永，滋味鲜爽回甘，汤色黄绿清澈，叶底嫩黄柔软。"}]}]}